@font-face {
    font-family: "Didot";
    src: url("../assets/fonts/Didot\ Regular.ttf");
}

@font-face {
    font-family: "Title-font";
    src: url("../assets/fonts/Khodijah.ttf");
}

@font-face {
    font-family: "Legend-font";
    src: url("../assets/fonts/Helvetica\ Neue\ Black\ Condensed\ Oblique.otf")
}

$small-screen: 740px;
$medium-screen: 960px;
$easeInOutCubic: cubic-bezier(0.645, 0.045, 0.355, 1.000);

* {margin:0; padding:0;}


body {
    background-color:#705152;
    transition: all 1000ms $easeInOutCubic;
}
body::-webkit-scrollbar{display: none;}
html {scrollbar-width: none;}

#DEBUG-TOOLS {position: fixed;top:0; left:0; z-index: 10000000;}
.DEBUG-NONE {display: none !important;}
.DEBUG-HIDDEN {visibility: hidden;}

.background {
    width: 100dvw; 
    height: 100dvh;
    max-width: 100%;
    position: fixed;
    z-index: -1;

    canvas {
        width: 100%; 
        height: 100%; 
        display:block;
    }

    #grain {
        position: absolute;
        top:0; left:0;
        width: 100%; height: 100%;
        background: url(../assets/textures/noise.png) repeat;
        opacity: 0.3;
        mix-blend-mode: soft-light;
    }
}

#loader-wrapper {
    position: absolute;
    align-self: center;
    top:0; left:0;
    width: 100%; height: 100%;
    display: flex; justify-content: center; align-items: center;
    opacity: 1;
    filter:blur(0px);
    transition: all 1000ms $easeInOutCubic;
    
    &.hidden {
        opacity: 0;
        filter:blur(50px);
    }

    #loader {
        width: 25vw;
        transition: width 500ms $easeInOutCubic;
        transition-delay: 1s;
    }
    #loader-bar {
        height: 20px;
        padding: 3px;
        box-sizing: border-box;
        border: #fff 3px solid;
    }
    #loader-bar-fill {
        width: 0%; height: 100%;
        background-color: #fff;
        transition: width 500ms $easeInOutCubic;
    }
}

#language-selector{
    display: flex;
    justify-content: space-evenly;
    margin-top: 10px;
    gap:20px;

    .language-option{
        font-family: "Didot", serif;
        height: 0px;
        font-size: 20pt;
        color:white;
        text-decoration: none;
        transition: all 500ms $easeInOutCubic;
        filter:blur(20px);
        opacity: 0;
    }

    &.visible .language-option{
        filter:blur(0px);
        opacity: 1;
        height: 20pt;
    }
}

.container{
    max-width:100%;
    width: 100dvw;
    height: 100dvh;
    z-index: 2;
}

.chapter{
    position: absolute;
    width: 100%;
    overflow-x: hidden;
    padding: 25dvh 0;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;

    &.none {
        display:none;
    }

    .title {
        width: 50%;
        margin-bottom: 50px;
        font-family: "Title-font", serif;
        font-size: min(max(2.5vw, 20pt), 36pt);
        color: white;
        text-align: center;
        align-self: center;
        transition: all 1000ms $easeInOutCubic;

        &.hidden{
            opacity: 0;
            filter: blur(20px);
            transform: translateY(50px);
        }
    }

    .fluid-width-content{
        display:flex;
        flex-direction: column;
        align-self: center;
        width: min(75vw, 1200px);
    }

    p{
        font-family: "Didot", serif;
        color:white;
        font-size: min(max(1.5vw, 12pt), 16pt);
        text-align:justify;
        z-index: 1;
        line-height: min(max(3vw, 24pt), 32pt);
        text-indent: 60pt;
        transition: all 1000ms $easeInOutCubic;

        &.hidden{
            opacity: 0;
            filter: blur(20px);
            transform: translateY(50px);
        }
    }

    .quote{
        padding: 50px 0;
        text-align: center;
        font-family: "Didot", serif;
        font-weight: bold;
        color:rgba($color: #fff, $alpha: 0.8);
        h2{
            width: max(20dvw, 400px);
            transition: opacity 1000ms $easeInOutCubic, filter 1000ms $easeInOutCubic;
            &.hidden{
                opacity: 0;
                filter: blur(20px);
                //transform: translateY(50px);
            }    
        }
    }
    .title::selection, p::selection, 
    .quote h2::selection, .read-more::selection, 
    span::selection, img::selection, 
    .legend::selection, li::selection{
        background-color: rgba(255, 255, 255, 0.25);
        color: white;
    }

    .image {
        display:flex;
        flex-direction: column;
    }

    .legend{
        margin-top:5px;
        letter-spacing: min(max(.1vw, 1pt), 1.2pt);
        font-family: "Legend-font", sans-serif;
        color:white;
        opacity: 0.7;
        font-size: min(max(1vw, 10pt), 12pt);
    }
    

    .paragraph-50-50{
        display:flex;
        gap: 3dvw;
        &.reversed{
            flex-direction: row-reverse;
        }
        & p{
            flex:50%;
        }
        & div{
            display: flex;
            flex:50%;
        }
    }

    .paragraph-30-70{
        display:flex;
        gap: 3dvw;
        &.reversed{
            flex-direction: row-reverse;
        }
        & p{
            flex:70%;
        }
        & div{
            display: flex;
            flex:30%;
        }
    }

    .paragraph-100 {
        display:flex;
        justify-content: center;
        & p{
            flex:100%;
        }
    }

    .chapter-controls{
        display:flex;
        padding: 50px 50px 0 50px;
        justify-content: end;
        &:has(.previous){
            justify-content: space-between;
        }
        
        .read-more, .previous{
            text-decoration: none;
            font-family: "Didot", serif;
            color:white;
            font-weight: bold;
            font-size: min(max(2.5vw, 20pt), 25pt);
            transition: all 500ms $easeInOutCubic;
            opacity: 0.5;

            &::after{
                content:'';
                display:block;
                width: 0;
                height: 3px;
                background: white;
                transition: width 500ms $easeInOutCubic;
            }
            &:hover{
                opacity: 1;
                &::after{
                    width: 100%;
                }
            }
            &.clicked::after{
                width: 0;
            }
            span{
                display:inline-block;
                transition: all 500ms $easeInOutCubic;
                opacity: 0;
                filter: blur(20px);
                transform: translateX(50px);
            }
        }
        &.visible{
            .read-more, .previous {
                span{
                    opacity: 1;
                    filter: blur(0px);
                    transform: translateX(0px);
                }
            }
        }
    }
}

.show-sm-only{
    display:none;
}

@media screen and (max-width: $medium-screen) {
    #loader-wrapper {
        #loader {
            width: 50vw;
        }
    }
    .chapter{
        .fluid-width-content{
            width: 90vw;
        }
    }
}

@media screen and (max-width: $small-screen) {
    #loader-wrapper {
        #loader {
            width: 75vw;
        }
    }
    .chapter{
        padding: 10dvh 0;

        .title {
            width: 70%;
        }

        p{
            text-indent: 30pt;
        }

        .quote{
            width: 100%;
        }

        .paragraph-50-50 {
            flex-direction: column;
            &.reversed{
                flex-direction: column-reverse;
            }
            & p{
                flex:100%;
            }
            & div{
                flex:100%;
            }
        }
        .paragraph-30-70{
            gap: 3dvw;
            flex-direction: column;
            &.reversed{
                flex-direction: column-reverse;
            }
            & p{
                flex:100%;
            }
            & div{
                flex:100%;
            }
        }
    }

    .sm-hidden{
        display:none;
    }

    .show-sm-only{
        display:block;
    }
}